$brk-base-colors: ();
$brk-base-colors: map-merge((
        "brand-primary":      #f83365,
        "secondary":          #c23381,
        "white":              #fff,
        "black":              #000,
        "brk-dark-base":      #272727,
        "brk-base-1":         #2c354c,
        "brk-base-2":         #f83365,
        "brk-base-3":         #f83365,
        "brk-base-4":         rgba(44,53,76,.86),
        "brk-base-5":         #f83365,
        "brk-base-6":         #f83365,
        "brk-base-7":         #f83365,
        "brk-base-8":         #f83365,
        "brk-secondary-1":    #2c354c,
        "brk-secondary-2":    #c23381,
        "brk-secondary-3":    #c23381,
        "brk-secondary-4":    #c23381,
        "brk-secondary-5":    #c23381,
        "brk-secondary-6":    #c23381,
				"text-base-color-1":  #c4c4c4,
				"text-base-color-2":  #c23381,
				"text-base-color-3":  #f3f3f3,
        "form-color-1":       #e5e5e5
), $brk-base-colors);

.colors{
  color: #f83365; //gradient   primary 
  color: #c23381; //gradient   secondary
  color: #2c354c; //gradient
  color: #26334f; //gradient
}

@function safe-rgb($color) {
	@return unquote("#{red($color)}, #{green($color)}, #{blue($color)}");
}

/* ----------- Palette variable ----------- */
:root {
	@each $color, $value in $brk-base-colors {
		--#{$color}: #{$value};
	}

	// RGB
	@each $color, $value in $brk-base-colors {
		--#{$color}-rgb: #{safe-rgb($value)};
	}

	--b-radius: inherit;
}
/* ----------- Palette variable ----------- */
.brk-simple-card-2 {
  min-height: 300px;
  width: 100%;
  display: flex;
  background-color: transparent;
  &_greyscale{
    .brk-simple-card-2__img{
      filter: grayscale(100%);
    } 
  }
  &__animation-wrapper {
    position: relative;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    flex-grow: 1;
    transform: scaleX(1) scaleY(1) scaleZ(1) translateX(0px) translateY(0px) translateZ(0px) rotateX(0deg) rotateY(0deg) rotateZ(0deg);
    will-change: transform;
  }
  &__img {
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    object-fit: cover;
    object-position: center;
    display: none;
  }
  &__overlay {
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    opacity: 0;
    transition: .3s ease opacity;
    will-change: opacity;
    display: none;
  }
  &__category {
    padding: 6px 9px 6px 9px;
    margin: 0 auto 17px;
    opacity: 0;
    transition: .3s ease opacity;
    will-change: opacity;
    box-shadow: 0 5px 16px rgba(var(--brand-primary-rgb), 0.5);
    background-color: var(--brand-primary);
  }
  &__info {
    display: flex;
    flex-direction: column;
    z-index: 1;
    opacity: 0;
    transition: .3s ease opacity;
    will-change: opacity;
  }
  .icon__btn {
    margin-top: 60px;
    margin-bottom: 2px;
    opacity: 0;
    transition: .3s ease opacity;
    will-change: opacity;
  }
  &:hover {
    .brk-simple-card-2 {
      &__overlay,
      &__category,
      &__info {
        opacity: 1
      }
      &__animation-wrapper {
        box-shadow: 0 5px 15px rgba(0, 0, 0, 0.3);
      }
    }
    .icon__btn {
      opacity: 1;
    }
  }
  &.rendered {
    .brk-simple-card-2__img,
    .brk-simple-card-2__overlay {
      display: block;
    }
  }
}

.brk-grid-fitrows {
  &__item {
    padding: 15px;
    @media screen and (max-width: 768px) {
      width: 100% !important;
    }
  }
  .brk-simple-card-2 {
    height: 100%;
  }
}